#breadcumb {
    padding: 0 8px;
    background-color: #F4F4F9;
    border-bottom: 1px solid rgba(38, 56, 64, 0.5);
}

.breadcrumb {
    background-color: #F4F4F9;
    margin-bottom: 0px;

}

.breadcrumb li a {
    max-width: 90%;
    display: inline-block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    vertical-align: middle;
}

@media screen and (min-width: 992px) {

    /* Start breadcumb style */
    #breadcumb {
        padding: 0 5%;
    }

    /* end breadcumb style */
}
